<template>
    <div class="weui-tabbar">
        <router-link to="/book" class="weui-tabbar__item">
            <div class="tabicon">
                <svg class="icon icon-active" aria-hidden="true">
                    <use xlink:href="#icon-zhuye1"></use>
                </svg>
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-zhuye1-copy"></use>
                </svg>
            </div>
            <p class="weui-tabbar__label">订房</p>
        </router-link>
        <router-link to="/meal" class="weui-tabbar__item" v-if="HotelInfo.roomReservationEnable==0">
            <div class="tabicon">
                <svg class="icon icon-active" aria-hidden="true">
                    <use xlink:href="#icon-liebiaocaidan"></use>
                </svg>
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-liebiaocaidan-copy"></use>
                </svg>
            </div>
            <p class="weui-tabbar__label">客房订餐</p>
        </router-link>
        <router-link to="/quit" class="weui-tabbar__item" v-if="HotelInfo.reservationCheckOutEnable==0">

            <div class="tabicon">
                <svg class="icon icon-active" aria-hidden="true">
                    <use xlink:href="#icon-tuichu"></use>
                </svg>
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-tuichu-copy"></use>
                </svg>
            </div>
            <p class="weui-tabbar__label">预约退房</p>
        </router-link>
        <router-link to="/administrators" class="weui-tabbar__item">
            <div class="tabicon">
                <svg class="icon icon-active" aria-hidden="true">
                    <use xlink:href="#icon-yonghu"></use>
                </svg>
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-yonghu-copy"></use>
                </svg>
            </div>
            <p class="weui-tabbar__label">我的</p>
        </router-link>
    </div>
</template>

<script type='text/ecmascript-6'>
export default {
  data() {
    return {
      HotelInfo: ""
    };
  },
  mounted() {
    this.$store.dispatch("getHotelInfo").then(() => {
      this.HotelInfo = this.$store.state.HotelInfo;
    });
  }
};
</script>

<style scoped lang='less' rel='stylesheet/less'>
.weui-tabbar {
  height: 1rem;
  line-height: 1;

  background: #ffffff;
  box-shadow: 0px 0px 9px rgba(53, 53, 53, 0.15); // inset
  right: 0;
  bottom: 0;
  left: 0;
  position: fixed;
  z-index: 1;
  svg {
    display: inline-block;
    height: 0.4rem;
    margin: 0 auto;
    width: 0.4rem;
  }
  .icon-active {
    display: none;
  }
  .weui-tabbar__item {
    padding: 0.15rem 0;
    color: #5f6379;
  }
  .tabicon {
    width: auto;
    height: auto;
  }
  p {
    font-size: 0.25rem;
    color: #5f6379;
  }
  .weui-tabbar__item.router-link-active {
    background-color: inherit;
    color: #3688ff;
    .icon {
      display: none;
    }
    .icon-active {
      display: block;
    }
    p {
      color: #3688ff;
    }
  }
}
</style>



